import axios from "axios";
import { message } from "antd";
export default (url, data, method='GET') => {
  if (method === "GET") {
    return new Promise((resolve, reject) => {
      axios
        .get(url, {
          params: data
        })
        .then(res => resolve(res.data))
        .catch(err => message.error("请求出错了:" + err));
    });
  } else {
    return new Promise((resolve, reject) => {
      axios
        .post(url, data)
        .then(res => resolve(res.data))
        .catch(err => message.error("请求出错了:" + err));
    });
  }
};
