import React, { Component } from 'react'
import {Card,Button,Table,message,Modal,Form,Input,Select} from 'antd'
import {PAGE_SIZE} from '../../utils/constant'
import {reqAddUser,reqUserList,reqUpdateUser,reqDeleUser} from '../../api/index'
import LinkButton from'../../components/link-button'
import AddForm from './add-form'
import ModifyForm from './modify-form'
import {getFormatDate} from '../../utils//utils'
const { confirm } = Modal;
const {Option} = Select
class User extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            userList:[],
            mVisible: 0,
            roles:[{id:'5d1acd1cc142743cb43d88ed'}]

         }
    }
    componentWillMount(){
        this.initColumns()
    }
    componentDidMount(){
        this.getUserList()
    }
    initColumns=()=>{
        this.columns = [
    
            {
              title: '用户名',
              dataIndex: 'username',
            },
            {
              title: '邮箱',
              dataIndex: 'email',
            },
            {
              title: '电话',
              dataIndex: 'phone',
            },
            {
                title: '注册时间',
                dataIndex: 'create_time',
                render:(time)=>{
                  let t = new Date(+time)
                  return getFormatDate(t)
                }
            },
            {
            title: '所属角色',
            dataIndex: 'roleName',
            
            },
            {
                title: '操作',
                render:(user)=>{
                    return (<span>
                        <LinkButton onClick={()=>this.showModifyModal(user)}>修改</LinkButton>
                        <LinkButton onClick={()=>this.showConfirm(user)}>删除</LinkButton>
                    </span>)
                    }
            }
            
          ]
    }
    getUserList=async ()=>{
        let res = await reqUserList()
        if(res.status===0){
            let userList = res.data.users
            let roles = res.data.roles
            let roleName = ''
            userList = userList.map((item)=>{

                roles.forEach((element)=>{
                
                    if(element.id===item.role_id){
                        roleName = element.name
                    }

                })
                return {...item,roleName}
            })
            this.setState({
                userList,
                roles
            
            })
            console.log(res)
        }
    }
    showAddModal = () => {
        this.setState({
          mVisible: 1,
        });
      };
      showModifyModal = (user) => {
        this.user = user
        this.setState({
          mVisible: 2,
        });
      };
    
      handleAdd = e => {
        this.form.validateFields( async (errs,values)=>{
            if(!errs){
                let username = this.form.getFieldValue('username')
                let email = this.form.getFieldValue('email')
                let role_id = this.form.getFieldValue('role_id')
                let phone = this.form.getFieldValue('phone')
                let password = this.form.getFieldValue('password')
                let res = await reqAddUser({
                    username,
                    email,
                    role_id,
                    phone,
                    password,
                   
                    
                })
                if(res.status===0){
                    this.getUserList()
                    message.success('添加成功')
                }else{
                    console.log(res)
                }
            }
          })
        this.setState({
          mVisible: 0,
        });
      };
      handleModify = e => {
        this.modifyForm.validateFields( async (errs,values)=>{
            if(!errs){
                let username = this.modifyForm.getFieldValue('username')
                let email = this.modifyForm.getFieldValue('email')
                let role_id = this.modifyForm.getFieldValue('role_id')
                let phone = this.modifyForm.getFieldValue('phone')
                let res = await reqUpdateUser({
                    username,
                    email,
                    role_id,
                    phone,
                    id:this.user.id
                })
                if(res.status===0){
                    this.getUserList()
                    message.success('修改成功')
                }
            }
          })
        this.setState({
          mVisible: 0,
        });
        this.setState({
          mVisible: 0,
        });
      };
    
      handleCancel = e => {
        
        this.setState({
          mVisible: 0,
        });
      };
     showConfirm = (user)=> {
        confirm({
          title: `确认删除${user.username}?`,
          onOk:async ()=> {
              let res = await reqDeleUser(user.id)
              if(res.status===0){
                  message.success('删除成功')
              }
              this.getUserList()
            console.log('OK');
          },
          onCancel() {
            
          },
        });
      }
    render() {
        const {userList,mVisible} = this.state
        const title = (
            <Button
                type='primary'
                onClick={this.showAddModal}
            >
                创建用户
            </Button>
        )
        
        
        return ( 
        <Card
            title={title}
        >
            <Table
            dataSource={userList}
            rowKey='id'
            columns={this.columns}
            bordered
            pagination={{pageSize:PAGE_SIZE}}
            >

            </Table>
            <Modal
            title="添加用户"
            visible={mVisible===1?true:false}
            onOk={this.handleAdd}
            onCancel={this.handleCancel}
            >
            <AddForm roles={this.state.roles} setParForm={(form)=>{this.form = form}}/>
            </Modal>
            <Modal
            title="修改用户"
            visible={mVisible===2?true:false}
            onOk={this.handleModify}
            onCancel={this.handleCancel}
            >
            <ModifyForm 
            roles={this.state.roles}
            user={this.user}
            setParForm={(form)=>{this.modifyForm = form}}
            />
            </Modal>
        </Card> 
        );
    }
}
 
export default Form.create()(User);