import React, { Component } from "react";
import { Card, Input, message, Button, Icon, Form, Cascader } from "antd";
import LinkButton from "../../components/link-button";
import { reqCategory } from "../../api/index";
import UploadImg from "./upload-img"
import RichEditor from './rich-editor'
import {reqAddUpdateProduct} from '../../api/index'
const { TextArea } = Input;
class AddUpdate extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }
  componentWillMount(){
      const product = this.props.location.state
      this.isUpdate=!!product
      this.product = product||{}
      this.defaultCategory = null
  }
  componentDidMount() {
    //初始化分类
    this.getCategories("0");
  }

  //处理分类状态
  getCategories = async id => {
    let res = await reqCategory(id);
    let options = [];
   
    if (res.status === 0 && res.data.length > 0) {
      options = res.data.map(item => {
        return {
          value: item.id,
          label: item.name,
          isLeaf: id === "0" ? false : true
        };
      });
    }
    //如果请求的一级分类，更新分类，否则返回二级分类
    if (id === "0") {

     //如果为更新页，处理默认分类，否则直接更新数据
      if(this.isUpdate&&this.product.pCategoryId!=='0'){
        let {pCategoryId,categoryId} = this.product
        let subCategory = await this.getCategories(pCategoryId)

        options = options.map((item)=>{
          if(item.value===pCategoryId){
            item.children = subCategory
          }
          return item
        })
        this.defaultCategory = [pCategoryId,categoryId]

      }else if(this.isUpdate){
        let {categoryId} = this.product
        options = options.map((item)=>{
          if(item.value===categoryId){
            item.isLeaf = true
          }
          return item
        })
        this.defaultCategory = [categoryId]
      }
      console.log(this.defaultCategory)
      this.setState({ options });
    } else {
      return new Promise(resolve => resolve(options));
    }
  };

  //加载选中分类的子分类信息，如果没有子分类，将当前设置为叶子
  loadData = async selectedOptions => {
    const targetOption = selectedOptions[selectedOptions.length - 1];
    targetOption.loading = true;
    let options = await this.getCategories(targetOption.value);
    targetOption.loading = false;
    if(options.length===0){
        targetOption.isLeaf = true
    }else{
        targetOption.children = options;
    }
    this.setState({
      options: [...this.state.options]
    });
    console.log(options);
  };

  //处理提交数据
  handleSubmit=()=>{
  
    this.props.form.validateFields( async (errs,values)=>{
      if(!errs){
        let imgs = this.uploadImg.getImgs()
        let detail = this.RichEditor.editorValue
        let name = this.props.form.getFieldValue('name')
        let desc = this.props.form.getFieldValue('desc')
        let price = this.props.form.getFieldValue('price')
        let category = this.props.form.getFieldValue('category')
        let categoryId,pCategoryId = ''
        if(category.length===1){
          categoryId = category[0]
          pCategoryId = '0'
        }else{
          categoryId = category[1]
          pCategoryId = category[0]
        }
        if(this.isUpdate){
          let id = this.product.id
          let res = await reqAddUpdateProduct({id,name,desc,detail,price,categoryId,pCategoryId,imgs})
          if(res.status===0){
            message.success('更新商品成功')
            this.props.history.goBack()
          }else{
            message.error('更新失败')
          }
        }else{
          let res = await reqAddUpdateProduct({name,desc,detail,price,categoryId,pCategoryId,imgs})
          if(res.status===0){
            message.success('添加商品成功')
            this.props.history.goBack()
          }else{
            message.success('添加失败')
          }
        }
        
      }
    })
  }
  onChange = (value, selectedOptions) => {
    console.log(value, selectedOptions);
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { options } = this.state;
    const {name,desc,price} = this.product
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 2 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      }
    };

    const title = (
      <div>
        <LinkButton
          onClick={() => {
            this.props.history.goBack();
          }}
        >
          <Icon style={{ fontSize: 20 }} type="arrow-left" />
        </LinkButton>
        <span>{this.isUpdate?'修改商品':'添加商品'}</span>
      </div>
    );
    return (
      <Card title={title}>
        <Form {...formItemLayout}>
          <Form.Item label="商品名称">
            {getFieldDecorator("name", {
              rules: [
                {
                  required: true,
                  message: "必须输入商品名称"
                }
              ]
              ,initialValue:name
            })(<Input placeholder="请输入商品名称" />)}
          </Form.Item>
          <Form.Item label="商品描述">
            {getFieldDecorator("desc", {
              rules: [
                {
                  required: true,
                  message: "必须输入商品描述"
                }
              ],
              initialValue:desc
            })(
              <TextArea
                placeholder="请输入商品描述"
                autosize={{ minRows: 2, maxRows: 6 }}
              />
            )}
          </Form.Item>
          <Form.Item label="商品价格">
            {getFieldDecorator("price", {
              rules: [
                {
                  required: true,
                  message: "必须输入商品价格"
                }
              ]
              ,initialValue:price
            })(
              <Input
                placeholder="请输入商品价格"
                type="number"
                addonAfter="元"
              />
            )}
          </Form.Item>
          <Form.Item label="商品分类">
            {getFieldDecorator("category", {
              rules: [
                {
                  required: true,
                  message: "必须输入商品分类"
                }
              ],
              initialValue:this.defaultCategory
            })(
              <Cascader
                placeholder='请选择商品分类'
                options={options}
                loadData={this.loadData}
                onChange={this.onChange}
                changeOnSelect
              />
            )}
          </Form.Item>
          <Form.Item label="商品图片">
            <span><UploadImg ref={(uploadImg)=>{this.uploadImg=uploadImg}} product={this.product}></UploadImg></span>
          </Form.Item>
          <Form.Item label="商品详情">
            <span><RichEditor ref={(re)=>{this.RichEditor=re}} productDetail={this.product.detail}/></span>
          </Form.Item>
          <Button type="primary" onClick={this.handleSubmit} >提交</Button>
        </Form>
      </Card>
    );
  }
}

export default Form.create()(AddUpdate);
