import React, { Component } from "react";
import { Card, Button, Table, Icon, Modal, message } from "antd";
import {
  reqCategory,
  reqUpdateCategory,
  reqAddCategory
} from "../../api/index";
import LinkButton from "../../components/link-button";
import AddForm from "./add-form";
import UpdateForm from "./update-form";
const columns = [
  {
    title: "分类的名称",
    dataIndex: "name",
    width: "75%"
  },
  {
    title: "操作",
    dataIndex: "linkButton",
    width: "25%"
  }
];
class Category extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: {},
      categories: [],
      subCategories: [],
      loading: false,
      parentName: "",
      parentId: "0"
    };
  }
  componentDidMount() {
   this.getCategoryData(0);
  }
  componentWillUnmount(){
    this.setState = (state,callback)=>{
      return;
    }
  }
  showAddModal = () => {
    this.setState({
      addModalVisible: true
    });
  };
  addCategory = async e => {
    this.formAdd.validateFields(async (err,values)=>{
      if(!err){
        
    let {categoryName,parentId} = values;
    let res = await reqAddCategory(parentId, categoryName);
    if (res.status === 0) {
      message.success("添加分类成功", 1);
      this.getCategoryData(parentId);
    }
    this.setState({
      addModalVisible: false
    });
    this.formAdd.resetFields();
      }
    });
    
  };

  handleAddCancel = e => {

    this.setState({
      addModalVisible: false
    });
    this.formAdd.resetFields();
  };
  /**
   * 确认修改分类
   */
  handleUpdateOk = e => {
    this.formUpdate.validateFields(async (err, values) => {
      if (!err) {
        let categoryId = this.categoryId;
        let { categoryName } = values;
        let res = await reqUpdateCategory(categoryId, categoryName);
        if (!res.status) {
          this.state.categories.forEach(element => {
            if (element.id === this.categoryId) {
              element.name = categoryName;
            }
          });
        }
        this.setState({
          updateModalVisible: false
        });
        this.formUpdate.resetFields();
      }
    });
  };

  handleUpdateCancel = e => {
    this.formUpdate.resetFields();
    this.setState({
      updateModalVisible: false
    });
  };
  modifyCategory = category => {
    this.setState({
      updateModalVisible: true
    });
    this.category = category;
    this.categoryId = category.id;
  };
  switchToSubCategory = async category => {
    let { data } = await reqCategory(category.id);
    let subCategories = data.map((item, index) => {
      item.key = item.id;
      item.linkButton = (
        <div>
          <LinkButton
            onClick={() => {
              this.modifyCategory(item);
            }}
          >
            修改分类
          </LinkButton>
        </div>
      );
      return item;
    });

    this.setState({
      subCategories,
      showSubCategories: true,
      parentName: category.name,
      parentId: category.id
    });
  };
  backToCategories = () => {
    this.setState({ showSubCategories: false, parentId: "0" });
  };
  getCategoryData = async parentId => {
    this.setState({ loading: true });
    let { data } = await reqCategory(parentId);
    if (parentId === 0) {
      let categories = data.map((item, index) => {
        item.key = item.id;
        item.linkButton = (
          <div>
            <LinkButton
              onClick={() => {
                this.modifyCategory(item);
              }}
            >
              修改分类
            </LinkButton>
            <LinkButton
              onClick={() => {
                this.switchToSubCategory(item);
              }}
            >
              查看子分类
            </LinkButton>
          </div>
        );
        return item;
      });
      this.setState({ categories }, () => {
        this.setState({ loading: false });
      });
    } else {
      let subCategories = data.map((item, index) => {
        item.key = item.id;
        item.linkButton = (
          <div>
            <LinkButton
              onClick={() => {
                this.modifyCategory(item);
              }}
            >
              修改分类
            </LinkButton>
          </div>
        );
        return item;
      });
      this.setState({ subCategories }, () => {
        this.setState({ loading: false });
      });
    }
  };
  seleOnChange = value => {
    this.parentId = value;
  };

  render() {
    let category = this.category || {};
    const title = this.state.showSubCategories ? (
      <div>
        <LinkButton onClick={this.backToCategories}>一级分类列表</LinkButton>
        <Icon style={{ marginRight: 7 }} type="arrow-right" />
        <span>{this.state.parentName}</span>
      </div>
    ) : (
      <div>一级分类列表</div>
    );
    const extra = (
      <Button onClick={this.showAddModal} type="primary" icon="plus">
        添加
      </Button>
    );
    return (
      <Card {...{ title, extra }} style={{ width: "100%", height: "100%" }}>
        <Table
          columns={columns}
          dataSource={
            this.state.showSubCategories
              ? this.state.subCategories
              : this.state.categories
          }
          bordered
          pagination={{ pageSize: 5, showQuickJumper: true }}
          loading={this.state.loading}
        />
        <Modal
          title="添加分类"
          visible={this.state.addModalVisible}
          onOk={this.addCategory}
          onCancel={this.handleAddCancel}
        >
          <AddForm
            categories={this.state.categories}
            parentId={this.state.parentId}
            seleOnChange={this.seleOnChange}
            passForm={form => {
              this.formAdd = form;
            }}
          />
        </Modal>
        <Modal
          title="修改分类"
          visible={this.state.updateModalVisible}
          onOk={this.handleUpdateOk}
          onCancel={this.handleUpdateCancel}
        >
          <UpdateForm
            passForm={form => {
              this.formUpdate = form;
            }}
            categoryName={category.name}
          />
        </Modal>
      </Card>
    );
  }
}

export default Category;
