import React, { Component } from "react";
import "./login.less";
import logo from "./images/logo.png";
import { Form, Icon, Input, Button, message } from "antd";
import { reqLogin } from "../../api/index.js";
import store from '../../config/storage';
import {setUser} from "../../utils/localstorage";
class Login extends Component {
  state = {};
  handleSubmit = async e => {
    e.preventDefault();
    let { form, history } = this.props;
    let formData = form.getFieldsValue(["username", "password"]);
    let res = await reqLogin({
      username: formData.username,
      password: formData.password
    });
    if (res.status === 0) {
      message.success("登录成功");
      setUser(res.data);
      store.user = res.data;
      history.replace("/");
    } else {
      message.error("用户名不存在或密码错误");
    }
  };
  async componentDidMount (){
    let res = await reqLogin({username:'admin',password:'admin'})
    console.log(res)
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="login-container">
        <header className="login-header">
          <img src={logo} alt="logo"/>
          <h1>V商城后台管理系统</h1>
        </header>
        <div className="login-content">
          <div className="login-form-wrap">
            <h2>用户登录</h2>
            <div className="login-antd-form">
              <Form onSubmit={this.handleSubmit} className="login-form">
                <Form.Item>
                  {getFieldDecorator("username", {
                    rules: [
                      { required: true, message: "用户名不能为空" },
                      { min: 4, message: "最小长度为4" },
                      { max: 12, message: "最大长度为12" }
                    ]
                  })(
                    <Input
                      prefix={
                        <Icon
                          type="user"
                          style={{ color: "rgba(0,0,0,.25)" }}
                        />
                      }
                      placeholder="用户名"
                    />
                  )}
                </Form.Item>
                <Form.Item>
                  {getFieldDecorator("password", {
                    rules: [
                      {
                        validator: (rule, value, callback) => {
                          if (!value) {
                            callback("请输入密码");
                          }
                        }
                      }
                    ]
                  })(
                    <Input
                      prefix={
                        <Icon
                          type="lock"
                          style={{ color: "rgba(0,0,0,.25)" }}
                        />
                      }
                      type="password"
                      placeholder="密码"
                    />
                  )}
                </Form.Item>
                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="login-form-button"
                  >
                    登录
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Form.create({ name: "normal_login" })(Login);
