import React, { Component } from "react";
import LinkButton from "../link-button";
import "./index.less";
import storage from "../../config/storage";
import { reqWeather } from "../../api/index";
import { removeUser } from "../../utils/localstorage";
import { message } from "antd";
import {withRouter} from "react-router-dom"
const menuItems = [
  {
    key: "/",
    title: "首页",
    icon: "pie-chart"
  },
  {
    key: "/goods",
    title: "商品",
    icon: "pie-chart",
    children: [
      {
        key: "/category",
        title: "品类管理",
        icon: "pie-chart"
      },
      {
        key: "/product",
        title: "商品管理",
        icon: "pie-chart"
      }
    ]
  },
  {
    key: "/user",
    title: "用户管理",
    icon: "pie-chart"
  },
  {
    key: "/role",
    title: "角色管理",
    icon: "pie-chart"
  },
  {
    key: "/charts",
    title: "商品",
    icon: "pie-chart",
    children: [
      {
        key: "/bar",
        title: "柱状图",
        icon: "pie-chart"
      },
      {
        key: "/pie",
        title: "饼状图",
        icon: "pie-chart"
      },
      {
        key: "/line",
        title: "线形图",
        icon: "pie-chart"
      }
    ]
  }
];
class HeaderLeft extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menuTitle: "首页",
      userName: "",
      time: "",
      weather: {}
    };
  }
  componentWillMount() {
    this.getUserName();
  }
  componentDidMount() {
    this.startTimeIterval();
    this.setWeather();
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }
  getUserName = () => {
    this.setState({ userName: storage.user.user.username });
  };

  getMenuTitle = () => {
    let path = this.props.location.pathname
    for (let value of menuItems) {
      if(value.key==='/'){
        if(value.key===path){
          return value.title
        }
        continue;
      }
      if (path.indexOf(value.key)>=0) {
        return value.title
        break;
      } else if (value.children) {
        for (let item of value.children) {
          if (path.indexOf(item.key)>=0) {
            return item.title;
            break;
          }
        }
      }
    }
  };

  logOut = () => {
    storage.user = {};
    removeUser("user");
    this.props.history.replace("/login");
    message.success("您已退出登录", 2);
  };
  startTimeIterval = () => {
    this.timer = setInterval(() => {
      let newTime = this.getFormatDate(new Date());
      this.setState({ time: newTime });
    }, 1000);
  };
  setWeather = async () => {
    let data = await reqWeather();
    let hours = new Date().getHours();
    if (hours < 18 && hours >= 6) {
      this.setState({ weather: { ...data, pictureUrl: data.dayPictureUrl } });
    } else {
      this.setState({ weather: { ...data, pictureUrl: data.nightPictureUrl } });
    }
    console.log(data, hours);
  };
  getFormatDate = (date) => {
    // console.log(Date());
    return (
      date.getFullYear() +
      "-" +
      (date.getMonth() + 1) +
      "-" +
      date.getDate() +
      "  " +
      date.getHours() +
      ":" +
      date.getMinutes() +
      ":" +
      date.getSeconds()
    );
  };
  render() {
    let menuTitle = this.getMenuTitle();
    return (
      <div className="admin-header-right">
        <div className="admin-header-top">
          <span>欢迎,{this.state.userName}</span>
          <LinkButton onClick={this.logOut}>退出</LinkButton>
        </div>
        <div className="admin-header-bottom">
          <span className="admin-header-bottom-left">
            {menuTitle}
          </span>
          <div className="admin-header-bottom-right">
            <span>{this.state.time}</span>
            <img src={this.state.weather.pictureUrl} alt="" />
            <span>{this.state.weather.weather}</span>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(HeaderLeft);
