import React, { Component } from "react";
import { Card, List, Icon } from "antd";
import LinkButton from "../../components/link-button/index";
import { reqCategoryInfo } from "../../api/index";
class ProductDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      categoryValue: ""
    };
  }
  componentWillMount() {
    this.product = this.props.location.state;
    this.getCategoryName(this.product.categoryId, this.product.pCategoryId);
  }
  getCategoryName = async (id, pId) => {
    if (pId === "0") {
      let res = await reqCategoryInfo(id);
      this.setState({ categoryValue: res.data.name });
    } else {
      let res = await reqCategoryInfo(pId);
      let res2 = await reqCategoryInfo(id);
      this.setState({ categoryValue: res.data.name+'-->'+res2.data.name });
    }
  };
  render() {
    const { name, desc, imgs, price,detail} = this.product;
    const {categoryValue} = this.state
    const title = (
      <div>
        <LinkButton
          onClick={() => {
            this.props.history.goBack();
          }}
        >
          <Icon type="arrow-left" style={{ fontSize: 20, marginRight: 10 }} />
        </LinkButton>
        <span>商品详情</span>
      </div>
    );
    return (
      <Card title={title}>
        <List
          itemLayout="horizontal"
        >
          <List.Item className="detail-list">
            <span className="detail-title">商品名称:</span>
            <span>{name}</span>
          </List.Item>
          <List.Item className="detail-list">
            <span className="detail-title">商品描述:</span>
            <span>{desc}</span>
          </List.Item>
          <List.Item className="detail-list">
            <span className="detail-title">商品价格:</span>
            <span>{price}</span>
          </List.Item>
          <List.Item className="detail-list">
            <span className="detail-title">所属分类:</span>
            <span>{categoryValue}</span>
          </List.Item>
          <List.Item className="detail-list">
            <span className="detail-title">商品图片:</span>
            {imgs.map((item, key) => {
              return (
                <img
                  key={key}
                  style={{ width: 130, border: "1px solid" }}
                  src={"/upload/" + item}
                  alt="product img"
                />
              );
            })}
          </List.Item>
          <List.Item className="detail-list">
            <span className="detail-title">商品详情:</span>
            <div dangerouslySetInnerHTML={{ __html: detail }} />
          </List.Item>
        </List>
        ,
      </Card>
    );
  }
}

export default ProductDetail;
