import React, { Component } from "react";
import {withRouter} from "react-router-dom"
import { Menu, Icon} from "antd";
import img from "../../assets/images/logo.png";
import './index.less';
import storage from '../../config/storage'
import {navMenus as menuItems} from '../../utils/constant'
const { SubMenu } = Menu;

const Rmenus = [
  "/product",
  "/products",
  "/category",
  "/user",
  "/role",
  "/charts",
  "/charts/bar",
  "/charts/line",
  "/charts/pie",
  "/order"
]
class NavLeft extends Component {
  constructor(props) {
    super(props);
    this.state = {
      defaultSelectedKey: "/",
      defaultOpenKey: "",
      menuItems: [],
      menuTitle: "首页",
    };
  }
  componentWillMount() {
    console.log(storage.user);
    this.menus = storage.user.role.menus
    let { pathname } = this.props.location;
    this.initialSiderMenu(pathname);
    this.setDefaultOpenKey(menuItems);
  }
  initialSiderMenu = pathname => {
    this.setState({
      menuItems: this.createMenus(menuItems),
      defaultSelectedKey: pathname
    });
  };
  setDefaultOpenKey = menuItems => {
    let { pathname } = this.props.location;
    
    for (let value of menuItems) {
      if (!value.children && pathname === value.key) {
        this.setState({ defaultOpenKey: pathname });
        break;
      } else if (value.children) {
        for (let item of value.children) {
          if (item.key === pathname) {
            this.setState({ defaultOpenKey: value.key });
            break;
          }
        }
      }
    }
  };
  createMenus = menuItems => {
    let menus = storage.user.username==="admin"?Rmenus:this.menus
    return menuItems.map((item, index) => {
      if (!item.children) {
        if(menus.includes(item.key)||item.key==='/'){
          return (
            <Menu.Item key={item.key} onClick={this.changeRoute}>
              <Icon type={item.icon} />
              <span>{item.title}</span>
            </Menu.Item>
          );
        }else{
          return ''
        }
      } else {
        if(menus.includes(item.key)||item.key==='/'){
          return (
            <SubMenu
              key={item.key}
              title={
                <span>
                  <Icon type={item.icon} />
                  <span>{item.title}</span>
                </span>
              }
            >
              {this.createMenus(item.children)}
            </SubMenu>
          );
        }else{
          return ''
        }
      }
    });
  };
  changeRoute = e => {
    this.props.history.push(e.key);
  };
  render() {
    let path = this.props.location.pathname
    console.log('render()', path)
    if(path.indexOf('/product')===0) { // 当前请求的是商品或其子路由界面
      path = '/product'
    }
    return (
      <div>
        <div
          onClick={() => {
            this.changeRoute({ key: "/" });
          }}
          className="admin-left-header"
        >
          <img src={img} alt="" />
          <h1>V商城后台</h1>
        </div>
        <div className="admin-left-menu">
          <Menu
            selectedKeys={[path]}
            defaultOpenKeys={[this.state.defaultOpenKey]}
            mode="inline"
            theme="dark"
            collapsed="true"
          >
            {this.state.menuItems}
          </Menu>
        </div>
      </div>
    );
  }
}

export default withRouter(NavLeft);
