import ajax from "./ajax.js";
import jsonp from 'jsonp';
export let reqLogin = (data) => ajax('/login', data, "POST");
export let reqWeather = ()=>{
    return new Promise((resolve,reject)=>{
        jsonp('http://api.map.baidu.com/telematics/v3/weather?location=成都&output=json&ak=3p49MVra6urFRGOT9s8UBWr2',(err,data)=>{
            if(err||data.error||!data.results){
                resolve({weather:'weather not found'});
                return;
            }
            resolve(data.results[0].weather_data[1]);
        });
    });

}
export let reqCategory = (parentId) =>ajax('/manage/category/list',{parentId});
export let reqUpdateCategory = (categoryId,categoryName)=>ajax('/manage/category/update',{categoryId,categoryName},"POST");
export let reqAddCategory = (parentId,categoryName)=>ajax('/manage/category/add',{parentId,categoryName},"POST");
/**
 * 通过搜索或直接请求获取商品
 */
export let reqProducts = ({pageNum,pageSize,seleValue,searchName})=>{
    if(!searchName){
        return ajax('/manage/product/list',{pageNum,pageSize});
    }else{
        return ajax('/manage/product/search',{
            pageNum,
            pageSize,
            [seleValue]:searchName
        });
    }
    
}

export let reqUpdateStatus = (productId,status)=>ajax('/manage/product/updateStatus',{productId,status},'POST')

export let reqCategoryInfo = (categoryId)=>ajax('/manage/category/info',{categoryId})

export let reqDeleImg = (imgName)=>ajax('/manage/img/delete',{name:imgName},'POST')

//添加或者更新商品
export let reqAddUpdateProduct = ({categoryId,pCategoryId,name,desc,price,detail,imgs,id})=>ajax(id?'/manage/product/update':'/manage/product/add',{
    categoryId,pCategoryId,name,desc,price,detail,imgs,id
},'POST')

export let reqRoleList = ()=>ajax('/manage/role/list')

export let reqAddRole = (roleName)=>ajax('manage/role/add',{roleName},'POST')

export let reqUpdateRole = (data)=>ajax('/manage/role/update',data,'POST')

export let reqAddUser =(data) =>ajax('/manage/user/add',data,'POST')

export let reqUserList = ()=>ajax('/manage/user/list')

export let reqUpdateUser = (data)=>ajax('/manage/user/update',data,'POST')

export let reqDeleUser = (id)=>ajax('/manage/user/delete',{userId:id},'POST')