import React, { Component } from "react";
import { Select, Input, Button, Icon, Card, Table, message } from "antd";
import LinkButton from "../../components/link-button/index";
import { reqProducts } from "../../api/index";
import {PAGE_SIZE} from "../../utils/constant";
import {reqUpdateStatus} from "../../api/index"
const { Option } = Select;

class ProductHome extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      total:'',
      pageSize:3,
      loading:false

    };
  }
  componentDidMount() {
    this.getProducts(1);
  }
  componentWillMount(){
      this.initColumns();
      this.seleValue = 'productName'
  }
  initColumns = ()=>{
      this.columns =[
        {
          title: "商品名称",
          dataIndex: "name",
          width: 300
        },
        {
          title: "商品描述",
          dataIndex: "desc"
        },
        {
          title: "价格",
          dataIndex: "price",
          render: value => "￥" + value,
          width: 120
        },
        {
          title: "状态",
          render: product => {
            return (
              <div>
                <Button type="primary" onClick={()=>{this.updateStatus(product)}}>{product.status === 1 ? "下架" : "上架"}</Button>
                {product.status === 1 ? "在售" : "已下架"}
              </div>
            );
          },
          width: 50
        },
        {
          title: "操作",
          render: value => {
            return (
              <div>
                <LinkButton onClick={
                  ()=>{
                    this.props.history.push({pathname:'/product/detail',state:value})
                  }
                }>详情</LinkButton>
                <LinkButton onClick={()=>{
                  this.props.history.push({pathname:'/product/addupdate',state:value})
                }}>修改</LinkButton>
              </div>
            );
          },
          width: 80
        }
      ]
  }
  updateStatus = async (product)=>{
    let res = await reqUpdateStatus(product.id,product.status===1?2:1)
    if(res.status===0){
        message.success('更新状态成功',2)
        this.getProducts(this.page)
    }

  }
  getProducts = async (e=1,seleValue='productName',searchName='') => {
    this.page = e;
    this.setState({loading:true});
    console.log(seleValue,searchName)
    let res = await reqProducts({
      pageNum:e,
      pageSize:PAGE_SIZE,
      seleValue,
      searchName
    });
    let {data} = res;
    console.log(data);
    this.setState({loading:false});
    if(res.status===0){
        this.setState({ products: data.list,total:data.total,pageSize:data.pageSize });
    }else{
        message.error('请求出错',2);
    }
    
  };
  render() {
    const { products,total,loading } = this.state;
    const title = (
      <div>
        <Select onChange={(value)=>{this.seleValue=value}} defaultValue="productName" style={{ width: 150 }}>
          <Option value="productName">按名称搜索</Option>
          <Option value="productDesc">按描述搜索</Option>
        </Select>
        <Input onChange={(e)=>{this.inputValue = e.target.value}} style={{ width: 150, margin: "0 15px" }} placeholder="关键字" />
        <Button onClick={()=>{this.getProducts(1,this.seleValue,this.inputValue)}} type="primary">搜索</Button>
      </div>
    );
    const extra = (
      <Button onClick={()=>{this.props.history.push('/product/addupdate')}} type="primary" icon="plus">
        添加商品
      </Button>
    );
    return (
      <Card {...{ title, extra }}>
        <Table
        loading={loading}
        rowKey="id" 
        columns={this.columns} 
        dataSource={products} 
        bordered
        pagination={{total,pageSize:PAGE_SIZE,onChange:(e)=>{this.getProducts(e,this.seleValue,this.inputValue)}}} 
        />
      </Card>
    );
  }
}

export default ProductHome;
