import React, { Component } from 'react'
import {Card,Button,Table,Modal,Form,Input,Tree,message} from 'antd'
import {reqRoleList,reqAddRole,reqUpdateRole} from '../../api/index'
import {PAGE_SIZE} from '../../utils/constant'
import storage from '../../config/storage'
import {navMenus} from '../../utils/constant'
import {getFormatDate} from '../../utils/utils'
const { TreeNode } = Tree;
  
const columns = [
    
    {
      title: '角色名称',
      dataIndex: 'name',
    },
    {
      title: '创建时间',
      dataIndex: 'create_time',
      render:(time)=>{
          let t = new Date(+time)
          return getFormatDate(t)
      }
    },
    {
      title: '授权时间',
      dataIndex: 'auth_time',
      render:(time)=>{
          if(time){
            let t = new Date(+time)
            return getFormatDate(t)
          }else{
              return ''
          }
        
    }
    },
    {
        title: '授权人',
        dataIndex: 'auth_name',
    },
  ];
  
class Role extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            roleList:[],
            selectedRowKeys:'',
            mVisible: 0,
            menus:[],
            roleName:'',
            autoExpandParent: true,
            selectedKeys: [],
         }
    }
    componentDidMount(){
      this.getRoleList()
    }
    getRoleList = async ()=>{
      let res = await reqRoleList()
      if(res.status===0){
        console.log(res.data)
        this.setState({roleList:res.data})
      }

    }
    onRow=record => {
        return {
          onClick: event => {
              this.setState({
                selectedRowKeys:record.id,
                menus:record.menus,
                roleName:record.name
              })
              
              console.log(record)
          }, // 点击行
        };
    }
    
    onSelectChange = selectedRowKeys => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);
        this.setState({ selectedRowKeys });
    };
    showAddModal = () => {
      this.setState({
        mVisible: 1,
      });
    };
    showSetModal = () => {
      this.setState({
        mVisible: 2,
      });
    };
  
    handleAddRole = e => {
      console.log(e);
      let form = this.props.form
      form.validateFields(async(errs)=>{
        if(!errs){
          let roleName = form.getFieldValue('roleName')
          let res = await reqAddRole(roleName)
          if(res.status===0){
            let roleList = [...this.state.roleList]
            roleList.push(res.data)
            message.success('添加成功')
            this.setState({
              mVisible: 0,
              roleList
            })
          }
        }
        
      })
    };
    handleSetRole = async e => {
      console.log(e);
      let id = this.state.selectedRowKeys
      let menus = this.state.menus.filter((item)=>item!=='0')
      let auth_name = storage.user.user.username
      let auth_time = Date.parse(new Date())
      let res = await reqUpdateRole({
        id,
        menus,
        auth_time,
        auth_name
      })
      if(res.status===0){
        console.log(res)
        this.getRoleList()
        message.success('设置成功')
        this.setState({
          mVisible: 0,
        });
      }
      
    };
  
    handleCancel = e => {
      console.log(e);
      this.setState({
        mVisible: 0,
      });
    };
    onSelect = (selectedKeys, info) => {
      console.log('selected', selectedKeys, info);
    };
  
    
    onExpand = expandedKeys => {
      console.log('onExpand', expandedKeys);
      // if not set autoExpandParent to false, if children expanded, parent can not collapse.
      // or, you can remove all expanded children keys.
      this.setState({
        expandedKeys,
        autoExpandParent: false,
      });
    };
  
    onCheck = checkedKeys => {
      console.log('onCheck', checkedKeys);
      this.setState({ menus:checkedKeys });
    };
  
    onSelect = (selectedKeys, info) => {
      console.log('onSelect', info);
      this.setState({ selectedKeys });
    };
    renderTreeNodes = data =>
    data.map(item => {
      if (item.children) {
        return (
          <TreeNode title={item.title} key={item.key} dataRef={item}>
            {this.renderTreeNodes(item.children)}
          </TreeNode>
        );
      }
      return <TreeNode {...item} />;
    });
    render() {
        const {getFieldDecorator} = this.props.form
        const {selectedRowKeys,roleList,mVisible,menus,roleName} = this.state
        const rowSelection = {
            selections:'',
            selectedRowKeys:this.state.selectedRowKeys,
            type:'radio',
            onChange: this.onSelectChange,
          }; 
        const title = (
            <div>
                <Button type='primary' style={{marginRight:15}} onClick={this.showAddModal}>创建角色</Button>
                <Button type='primary' disabled={selectedRowKeys?false:true} onClick={this.showSetModal}>设置角色权限</Button>
            </div>
        )
        const formItemLayout = {
          labelCol: {
            xs: { span: 20 },
            sm: { span: 5 }
          },
          wrapperCol: {
            xs: { span: 20 },
            sm: { span: 18 }
          }
        };
        return ( 
            <Card
            title = {title}
            >
                <Table
                    onRow={this.onRow}
                    rowSelection={rowSelection}
                    rowKey='id'
                    dataSource={roleList} 
                    columns={columns}
                    bordered
                    pagination={{pageSize:PAGE_SIZE}}
                />
                <Modal
                  title="添加角色"
                  visible={mVisible===1?true:false}
                  onOk={this.handleAddRole}
                  onCancel={this.handleCancel}
                >
                  <Form {...formItemLayout}>
                    <Form.Item label="角色名称">
                      {getFieldDecorator("roleName", {
                        rules: [
                          {
                            required: true,
                            message: "必须输入角色名称"
                          }
                        ]
                        ,initialValue:''
                      })(<Input placeholder="请输入角色名称" />)}
                    </Form.Item>
                  </Form>
                </Modal>
                <Modal
                  title="设置角色权限"
                  visible={mVisible===2?true:false}
                  onOk={this.handleSetRole}
                  onCancel={this.handleCancel}
                >
                  <Form 
                    {...{
                      labelCol: {
                        xs: { span: 4 },
                        sm: { span: 0 }
                      },
                      wrapperCol: {
                        xs: { span: 0 },
                        sm: { span: 12 }
                      }
                    }}
                  >
                    <Form.Item label="角色名称">
                      {getFieldDecorator("name", {
                        initialValue:''
                      })(<Input placeholder={roleName} disabled/>)}
                    </Form.Item>
                  </Form>

                  <Tree
                    checkable
                    defaultExpandAll
                    autoExpandParent={this.state.autoExpandParent}
                    onCheck={this.onCheck}
                    checkedKeys={menus}
                    onSelect={this.onSelect}
                    selectedKeys={this.state.selectedKeys}
                  >     
                    <TreeNode title="平台权限" key="0">
                      {this.renderTreeNodes(navMenus)}
                    </TreeNode>
                  </Tree>
                </Modal>
            </Card>
            );
    }
}
 
export default Form.create()(Role);