import React, { Component } from "react";
import { Upload, Icon, Modal,message } from "antd";
import {reqDeleImg} from '../../api/index'
function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}
class UploadImg extends Component {
  constructor(props) {
    super(props);
    this.state = {
      previewVisible: false,
      previewImage: "",
      fileList: []
    };
  }
  componentDidMount(){
      this.initFileList()
  }
  initFileList = ()=>{
    let {imgs} = this.props.product
    let fileList = []
    if(imgs&&imgs.length>0){
        fileList = imgs.map((item,index)=>{
            return{
                uid:index,
                name:item,
                status:'done',
                url:'/upload/'+item
            }

        })
    }
    this.setState({fileList})
  }
  handleCancel = () => this.setState({ previewVisible: false });

  //父组件获取数据
  getImgs = ()=>{
    return this.state.fileList.map((item)=>{
      return item.name
    })
  }
  handlePreview = async file => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    this.setState({
      previewImage: file.url || file.preview,
      previewVisible: true
    });
  };

  //file的状态有uploading、done、removed、error
  handleChange = async ({ fileList,event,file }) => {
   
    if(file.status==='done'){
        console.log(file)
        let len = fileList.length-1
        if(file.response.status===0){
            let name = file.response.data.name
            message.success('上传图片成功！')
            fileList[len] = {
                uid:len,
                status:'done',
                name,
                url:'/upload/'+name
            }
        }
    }else if(file.status==='removed'){
        console.log(file)
        let res = await reqDeleImg(file.name)
        if(res.status === 0){
            message.success("删除图片成功！")
        }
    }
    this.setState({ fileList })
  };

  render() {
    const { previewVisible, previewImage, fileList } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    return (
      <div className="clearfix">
        <Upload
          action="/manage/img/upload"
          listType="picture-card"
          name="image"
          fileList={fileList}
          onPreview={this.handlePreview}
          onChange={this.handleChange}
        >
          {fileList.length >= 3 ? null : uploadButton}
        </Upload>
        <Modal
          visible={previewVisible}
          footer={null}
          onCancel={this.handleCancel}
        >
          <img alt="example" style={{ width: "100%" }} src={previewImage} />
        </Modal>
      </div>
    );
  }
}

export default UploadImg;
